<template>
  <div id="adoutUs">
    <head-vue title="About Us" goBack="true"></head-vue>
    <div class="connect">
      <img src="../../assets/me/logo@3x.png" alt="">
      <span>growroomate</span>
      <span>{{version}}</span>
    </div>
  </div>
</template>
<script>
import head from '../../components/common/head'

import {
  versionNumberRequest
} from '../../api/method'
import {
  versionNumberResponse
} from '../../api/getData'
export default {
  data () {
    return {
      version: null
    }
  },
  components: {
    'head-vue': head
  },
  created () {
    versionNumberRequest(this.NAVIGATOR, this)
    versionNumberResponse(this.NAVIGATOR, this)
  }
}
</script>
<style lang="less" scoped>
#adoutUs{
  .fs;
  z-index: 999;
  background: white;
  .connect{
    .wh(100%, 100%);
    margin-top: 114px;
    display: flex;
    flex-direction: column;
    img{
      .wh(253px,253px);
      align-self: center
    }
    span:nth-child(2) {
      font-size: 46px;
      color: #585858;
      margin-top: 23px;
    }
    span:nth-child(3) {
      font-size: 22px;
      color: #595959;
      margin-top: 63px;
    }
  }
}
</style>



